<template>
    <v-content>
        <v-container>
            <v-form @submit="login">
                <v-text-field v-model="user" :rules="controlUser" label="Usuario" prepend-icon="mdi-account"></v-text-field>
                <v-text-field v-model="pass" :rules="controlPass" label="Contrasea" type="password"
                    prepend-icon="mdi-lock"></v-text-field>
                <v-btn class="primary mt-3" block @click="methodLogin">Iniciar Sesin</v-btn>
                <div style="margin-top: 20px;">
                    <a @click="cambioContrasea()" class="mail">Has olvidado la contrasea?</a>
                </div>
            </v-form>
        </v-container>
    </v-content>
</template>
<script>
import { mapActions, mapGetters, mapMutations } from 'vuex';
import CryptoJS from 'crypto-js';
import emailjs from '@emailjs/browser';
export default {

    name: "FormUser",

    data: () => ({
        user: '',
        pass: '',
        message: '',
        //permisosDirectorios: new Map(),

        controlUser: [
            value => {
                if (value) return true
                return 'Debes ingresar un Usuario'
            },
        ],
        controlPass: [
            value => {
                if (value) return true
                return 'Debes ingresar una Contrasea'
            },
        ],

    }),

    created() {
        this.cargarSubCarpetas();
    },

    methods: {
        ...mapActions('Login', ['login', 'validarCorreo', 'cambioContraseaCorreo']),
        ...mapActions('SubCarpetas', ['cargarSubCarpetas']),

        recuperarContrasea( email, pass) {
            let sms = {
                to_email: email,
                message: pass,
                from_name: 'UTA Services',
            };

            emailjs.send('service_jafj6oc', 'template_a6j4kwb', sms, '_9AGnDi4yMhKta2Fh')
                .then(response => {
                    this.$alertify.alert('Cambio de contrasea', 'Se ha enviado un correo con una contrasea temporal', () =>
                        this.$alertify.warning('alerta cerrada')
                    );
                })
                .catch(error => {
                    this.$alertify.alert('Cambio de contrasea', 'El servicio no esta disponible', () =>
                        this.$alertify.warning('alerta cerrada')
                    );
                });
        },

        generarContrasea() {
            var chars = "abcdefghijklmnopqrstubwsyzABCDEFGHIJKLMNOPQRSTUVWXYZ1234567890";
            var password = '';
            for (let i = 0; i < 10; i++) {
                password += chars.charAt(Math.floor(Math.random() * chars.length));
            }
            return password;
        },

        cambioContrasea() {
            this.$alertify.promptWithTitle(
                'Recuperar contrasea',
                'Digite su correo para poder seguir con el proceso',
                ' ',
                async (evt, value) => {
                    try {
                        if ( value != '' ) {
                            const correoValido = await this.validarCorreo({ correo: value });
                            if (correoValido) {
                                let auxC = this.generarContrasea();
                                let auxContra = CryptoJS.SHA256( auxC ).toString();
                                let cambio = await this.cambioContraseaCorreo( { correo: value, newPass: auxContra } );
                                if( cambio ){
                                    this.recuperarContrasea( value, auxC );
                                }else{
                                    this.$alertify.error('Error con el servidor');
                                }
                            } else {
                                this.$alertify.error('El correo ingresado no existe');
                            }
                        }else{
                            this.$alertify.error('Ingrese un correo vlido');
                        }
                    } catch (error) {
                        console.error('Error:', error);
                    }
                },
                () => this.$alertify.error('cancel')
            );
        },


        methodLogin: async function () {
            // Llama a la accin 'login' para autenticar al usuario.
            let auxContra = CryptoJS.SHA256(this.pass).toString();
            await this.login({ user: this.user, pass: auxContra });
            const autenticacion = localStorage.getItem('Authentication');
            //console.log(this.autenticacion);
            if (autenticacion) {
                this.$router.push("/Estudiantes");
                //this.obtenerPermisosDirectorios();
            } else {
                this.$alertify.error("Usuario no encontrado");
            }
        },

    },

    computed: {
        ...mapGetters('Login', ['getUser', 'getAuthentication', 'getSms']),
    }
}
</script>

<style>
v-card-text {
    font-size: 40px;
}

.miStyle {
    color: blue;
}

.mail {
    display: flex;
    align-items: center;
    justify-content: center;
}

.mail:hover {
    text-decoration: underline;

}
</style>