<template>
    <div>
        <div style="overflow: hidden;">
        </div>
        <v-card class="mx-auto" max-width="200">
            <v-img :src="(urlDw != null) ? urlDw : imgAux" contain height="100px" cover></v-img>

            <v-card-title class="d-flex justify-center align-center" style="font-size: 15px;">
                {{ titulo }}
            </v-card-title>

            <v-card-actions>
                <v-btn color="error darken-2" @click="eliminar" style="display: none;">
                    <template>
                        <v-icon>mdi-delete</v-icon>
                    </template>
                </v-btn>
                <v-btn color="primary darken-4" @click="editar()" icon small>
                    <template>
                        <v-icon>mdi-pencil</v-icon>
                    </template>
                </v-btn>

                <v-spacer></v-spacer>

                <v-btn @click="show = !show" icon small>
                    <template v-if="show">
                        <v-icon>mdi-chevron-up</v-icon>
                    </template>
                    <template v-else>
                        <v-icon>mdi-chevron-down</v-icon>
                    </template>
                </v-btn>

            </v-card-actions>

            <v-expand-transition>
                <div v-show="show">
                    <v-divider></v-divider>
                    <v-card-text v-for="(word, index) in words" :key="index">
                        <ul style="font-size:10px;overflow: hidden;list-style: none;">
                           <li>
                            <strong>
                                <v-icon class="mr-3" size="16" color="yellow darken-1">mdi-folder</v-icon>
                                {{ word }}
                            </strong>
                           </li> 
                        </ul>
                    </v-card-text>
                </div>
            </v-expand-transition>
        </v-card>
    </div>
</template>

<script>

import { mapState, mapMutations, mapActions } from 'vuex';

export default {
    data: () => ({
        show: false,
        words: [],
        imgAux: require('../assets/plantilla.png'),

    }),

    props: [
        'idPlan', 'titulo', 'idItem', 'items', 'urlDw',
    ],

    methods: {
        ...mapMutations('Plantillas', ['setPlan']),
        ...mapMutations('Dialogo', ['setDialogPlantilla']),

        editar() {
            try {
                this.setDialogPlantilla(true);
                this.words = [this.items];
                this.words = this.items.split(",");
                this.plantillaSelect = {
                    idPlan: this.idPlan,
                    nomPlan: this.titulo,
                    items: this.words,
                }
                //console.log(this.plantillaSelect);
                this.setPlan(this.plantillaSelect);
            } catch (error) {
                this.$alertify.error('Error al editar:', error);
            }
        },

        eliminar() {
            this.itemSeleccionadoPlan = {
                id: this.idPlan,
                nombre: this.titulo,
            };
            this.$alertify.confirm(
                'Deseas eliminar la Plantilla: ' + this.itemSeleccionadoPlan.nombre,
                () => {
                    this.eliminarUsuario(this.itemSeleccionadoPlan);
                    //console.log(this.itemSeleccionadoPlan);
                    this.$alertify.success('Plantilla  Eliminada');
                },
                () => this.$alertify.error('Cancelado')
            );
        },
    },

    mounted() {
        // Separar la cadena en palabras utilizando la coma como delimitador
        this.words = this.items.split(",");
    },

    computed: {
    }
}
</script>