import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default {
  namespaced: true,

  state: {
    dialogUser: false, // Estado del dilogo
    dialogFolder: false, // Estado del dilogo
    dailogPlantilla:false, // Estado del dilogo
    dailogCarpeta:false, // Estado del dilogo
    dailogPermisos:false, // Estado del dilogo
    dailogProgres:false, // Estado del dilogo
    dailogCarreras:false, // Estado del dilogo
    dailogContra:false, // Estado del dilogo
    dailogCarpetas:false, // Estado del dilogo
    dailogCrearEst:false, // Estado del dilogo
    dailogPermisos:false, // Estado del dilogo
    dailogSubirArch:false, // Estado del dilogo
    dailogPlantillaProgress:false, // Estado del dilogo
    dailogCargarDatos:false, // Estado del dilogo
    dailogEliminar:false, // Estado del dilogo
    mostrarCrear:false, // Estado del dilogo
    tablaEst: false, // Estado del dilogo
    tablaArch: false, // Estado del dilogo
    carreras: true, // Estado del dilogo
    ctlSubirArch:false,
    ctlfolder:false,//servira para poder modificar las carpetas creadas en donde se puede subir archivos

    itemsBread:['CARRERAS',],
    
  },

  mutations: {
    setDialog(state, value) {
      state.dialogUser = value; // Mutacin para actualizar el estado del dilogo
    },

    setDialogFolder(state, value) {
      state.dialogFolder = value; // Mutacin para actualizar el estado del dilogo
    },

    setDialogPlantilla(state, value) {
      state.dailogPlantilla = value; // Mutacin para actualizar el estado del dilogo
    },

    setDialogCarpeta(state, value) {
      state.dailogCarpeta = value; // Mutacin para actualizar el estado del dilogo
    },

    setDialogPermisos(state, value) {
      state.dailogPermisos = value; // Mutacin para actualizar el estado del dilogo
    },

    setDialogProgres(state, value) {
      state.dailogProgres = value; // Mutacin para actualizar el estado del dilogo
    },

    setDialogCarreras(state, value) {
      state.dailogCarreras = value; // Mutacin para actualizar el estado del dilogo
    },

    setDailogContra(state, value) {
      state.dailogContra = value; // Mutacin para actualizar el estado del dilogo
    },

    setDailogCarpetas(state, value) {
      state.dailogCarpetas = value; // Mutacin para actualizar el estado del dilogo
    },

    setDailogPermisos(state, value) {
      state.dailogPermisos = value; // Mutacin para actualizar el estado del dilogo
    },

    setDailogSubirArch(state, value) {
      state.dailogSubirArch = value; // Mutacin para actualizar el estado del dilogo
    },

    setDailogPlantillaProgress(state, value) {
      state.dailogPlantillaProgress = value; // Mutacin para actualizar el estado del dilogo
    },

    setDailogCargarDatos(state, value) {
      state.dailogCargarDatos = value; // Mutacin para actualizar el estado del dilogo
    },

    setMostrarCrear(state, value) {
      state.mostrarCrear = value; // Mutacin para actualizar el estado del dilogo
    },

    setDailogCrearEst(state, value) {
      state.dailogCrearEst = value; // Mutacin para actualizar el estado del dilogo
    },

    setDailogEliminar(state, value) {
      state.dailogEliminar = value; // Mutacin para actualizar el estado del dilogo
    },

    setVentanaEst(state, value) {
      state.tablaEst = value; // Mutacin para actualizar el estado del dilogo
    },
    setVentanaArch(state, value) {
      state.tablaArch = value; // Mutacin para actualizar el estado del dilogo
    },

    setVentanaCarreras(state, value) {
      state.carreras = value; // Mutacin para actualizar el estado del dilogo
    },

    setCtlSubirArch(state, value) {
      state.ctlfolder = value; // Mutacin para actualizar el estado del dilogo
    },

    setCtlfolder(state, value) {
      state.ctlSubirArch = value; // Mutacin para actualizar el estado del dilogo
    },

    setBreadcrumbs(state, value) {
      if( value == '' ){
        //console.log('Borrra');
        state.itemsBread.splice(1, state.itemsBread.length); 
      }else{
        //buscar si el parametro existe ? borramos : agregamos
        const found = state.itemsBread.find( (item) => item == value );
        if( found ){
          let index = state.itemsBread.indexOf(value);
          state.itemsBread.splice(index+1,state.itemsBread.length);
        }else{
          state.itemsBread.push(value); 
        }
      }
    },
  }
};